<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                              <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="zhanghao">
          <el-input v-model="ruleForm.zhanghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='huiyuan'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in huiyuanxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="lianxidianhua">
          <el-input v-model="ruleForm.lianxidianhua" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="dianziyouxiang">
          <el-input v-model="ruleForm.dianziyouxiang" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="youzhengbianma">
          <el-input v-model="ruleForm.youzhengbianma" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='huiyuan'"  label="" prop="jiatingdizhi">
          <el-input v-model="ruleForm.jiatingdizhi" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='huiyuan'" label="" prop="gerenzhaopian">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.gerenzhaopian?ruleForm.gerenzhaopian:''"
          @change="huiyuangerenzhaopianUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                                                                                                                                                                                                            <el-form-item v-if="flag=='users'" label="" prop="username">
        <el-input v-model="ruleForm.username" 
        placeholder=""></el-input>
      </el-form-item>
      <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="onUpdateHandler"> </el-button>
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
                                                                  huiyuanxingbieOptions: [],
                                                                                                                                                                                                                                                                };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                            this.huiyuanxingbieOptions = ",".split(',')
                                                                                                                                                                          },
  methods: {
                                                                                                                                                                                                                                                                                                huiyuangerenzhaopianUploadChange(fileUrls) {
        this.ruleForm.gerenzhaopian = fileUrls;
    },
                                                                                                                                    onUpdateHandler() {
                                                                                                                                                                                                                                                if( 'huiyuan' ==this.flag && this.ruleForm.lianxidianhua&&(!isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                  if( 'huiyuan' ==this.flag && this.ruleForm.dianziyouxiang&&(!isEmail(this.ruleForm.dianziyouxiang))){
        this.$message.error(``);
        return
      }
                                          if( 'huiyuan' ==this.flag && this.ruleForm.youzhengbianma&&(!isIntNumer(this.ruleForm.youzhengbianma))){
       this.$message.error(``);
        return
      }
                                                                                                                                                                  if( 'huiyuan' ==this.flag && this.ruleForm.money&&(!isNumber(this.ruleForm.money))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                  this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
