<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="160px"
    >
      <el-form-item label="" prop="title">
        <el-input v-model="ruleForm.goodname" placeholder="" readonly></el-input>
      </el-form-item>
      <el-form-item label="" prop="picture">
        <img :src="ruleForm.picture" style="width:100px;">
      </el-form-item>
      <el-form-item label="" prop="buynumber">
        <el-input v-model="ruleForm.buynumber" placeholder="" readonly></el-input>
      </el-form-item>
      <el-form-item label="/" prop="price">
        <el-input v-model="ruleForm.price" placeholder="/" readonly></el-input>
      </el-form-item>
      <el-form-item label="/" prop="total">
        <el-input v-model="ruleForm.total" placeholder="/" readonly></el-input>
      </el-form-item>
      <el-form-item label="" prop="discounttotal">
        <el-input v-model="ruleForm.discounttotal" placeholder="" readonly></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-tag v-if="ruleForm.type=='1'" type="success"></el-tag>
        <el-tag v-if="ruleForm.type=='2'" type="info"></el-tag>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="ruleForm.address" placeholder="" readonly></el-input>
      </el-form-item>
      <el-form-item v-if="status == ''" label="" prop="status">
        <el-select v-model="ruleForm.status" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button v-if="ruleForm.status == ''" type="primary" @click="onSubmit"></el-button>
        <el-button @click="back()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {},
      status: "",
      rules: {}
    };
  },
  props: ["parent"],
  methods: {
    // 
    init(id) {
      this.$http({
        url: `${this.$api.orderinfo}${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
          this.status = this.ruleForm.status;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
    // 
    onSubmit() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${
              !this.ruleForm.id
                ? `${this.$api.ordersave}`
                : `${this.$api.orderupdate}`
            }`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // 
    back() {
      this.parent.showFlag = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.editor {
  height: 500px;
}
</style>
